import React from "react";
import styled from "@emotion/styled";
import { motion } from "framer-motion";

const Container = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
`;

//     
const containerVariants = {
  hidden: { opacity: 0, y: 20 }, //      
  visible: {
    opacity: 1,
    y: 0, //    
    transition: {
      staggerChildren: 0.3, //      
      when: "beforeChildren", //      
    },
  },
};

//      
const childVariants = {
  hidden: { opacity: 0 },
  visible: {
    opacity: 1,
    transition: {
      duration: 0.5, //      
    },
  },
};

const Text = styled(motion.h1)`
  display: flex;
  justify-content: center;
  background: #000;
  color: #fff;
  font-size: 4rem; //   
  @media (max-width: 768px) {
    font-size: 2rem; //  768px    
  }
`;

const SubText = styled(motion.h2)`
  font-size: 3rem; //   
  @media (max-width: 768px) {
    font-size: 1.5rem; //  768px    
  }
`;

const NextButton = styled(motion.div)`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 50px;
  border: 1px solid #000;
  font-size: 1.5rem; //     
  font-weight: bold;
  cursor: pointer;
  z-index: 10; /*   z-index  */
  @media (max-width: 768px) {
    font-size: 0.8rem; /*       */
    min-width: 80px; /*       */
  }
`;

function Home() {
  const scrollToSection = (sectionId) => {
    const section = document.getElementById(sectionId);
    if (section) {
      section.scrollIntoView({ behavior: "smooth" });
    }
  };
  return (
    <Container id="home">
      <motion.div
        className="home"
        variants={containerVariants}
        initial="hidden"
        animate="visible"
        style={{ padding: "1rem" }}
      >
        <Text variants={childVariants} style={{ fontSize: "50px" }}>
          LEE SUNG KYU
        </Text>
        <SubText variants={childVariants} style={{ fontSize: "40px" }}>
             
        </SubText>

        <NextButton
          variants={childVariants}
          whileHover={{ scale: 1.1 }} //      
          whileTap={{ scale: 0.95 }} // ()     
          onClick={() => scrollToSection("about")} //   /about  
        >
          Next
        </NextButton>
      </motion.div>
    </Container>
  );
}

export default Home;
